.container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.conatiner-header{
  flex: 0 0 auto;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 0;
}

.container-body{
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.tab-box{
  flex: 0 0 auto;
}

.image-container{
  flex: 1 1 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  overflow-y: auto;
}

.image-box{
  flex: 0 0 calc((100% - 20px) / 3);
  aspect-ratio: 4 / 3;
  overflow: hidden;
  box-sizing: border-box;

  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}

